<template>
  <div class="scoreboard">
    <v-card
      :color="getColor(index)"
      v-for="(player, index) in scoreSorted"
      :key="index"
      elevate="2"
      width="100%"
      style="position: relative"
    >
      <v-row no-gutters>
        {{ index + 1 }}. {{ player.name }} Score: {{ player.score }}
      </v-row>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "ScoreBoard",
  props: {
    msg: String,
  },
  data: () => {
    return {
      guess: "",
    };
  },
  computed: {
    scoreSorted() {
      return this.$store.getters.getPlayersByScore.filter(player => player.score > 0);
    },
  },
  methods: {
    getColor: function(index) {
      if (index === 0) {
        return "#e2c226";
      } else if (index === 1) {
        return "#9c9a9a";
      } else if (index === 2) {
        return "#7b5707";
      } else {
        return "white";
      }
    },
  },
  components: {},
};
</script>

