<template>
  <v-row class="hundredAlmost">
    <v-row>
      <v-col>
        <v-btn color="white"> Turn Timer: {{ turnTimer }} </v-btn>
      </v-col>
      <v-col>
        <v-btn color="white" @click="skip"> Finish Turn </v-btn>
      </v-col>
    </v-row>
    <v-row class="hundred">
      <canvas
        id="canvas"
        width="800px"
        height="500px"
        @mousedown="drawEnabled"
        @mouseup="drawDisabled"
        @mousemove="draw"
        @touchmove="draw"
        @touchstart="drawEnabled"
        @touchend="drawDisabled"
      >
      </canvas>
    </v-row>
  </v-row>
</template>

<script>
import {
  MOUSE_MOVE,
  MOUSE_UP,
  MOUSE_DOWN,
  SET_CANVAS,
  FINISH_TURN,

} from "../constants/mutation-types";

export default {
  name: "Canvas",
  props: {},
  data: () => {
    return {
      now: Date.now(),
    };
  },
  methods: {

    skip: function () {
        if (this.$store.getters.isActive) {
            this.$store.commit(FINISH_TURN)
        }
    },
    draw: function(e) {
        if (e) {
            e.preventDefault()
        }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_MOVE, { x: e.offsetX, y: e.offsetY });
      }
    },
    drawEnabled: function(e) {
        if (e) {
            e.preventDefault()
        }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_DOWN, { x: e.offsetX, y: e.offsetY });
      }
    },
    drawDisabled: function(e) {
        if (e) {
            e.preventDefault()
        }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_UP, {});
      }
    },
  },
  computed: {
    turnOrder() {
      return this.$store.state.turnOrder;
    },
    turnTimer() {
      let time = new Date(this.$store.state.turnTimeEnd).getTime();
      return Math.round((time - this.now) / 1000);
    },
    playerMap() {
      return this.$store.state.playerMap;
    },
  },
  mounted() {
    var c = document.getElementById("canvas");
    var ctx = c.getContext("2d");
    this.$store.commit(SET_CANVAS, { ctx });
  },
  created() {
    setInterval(() => {
      this.now = Date.now();
    }, 1000);
  },
};
</script>

