import Vue from 'vue'
import App from './App.vue'
import VueCookies from 'vue-cookies'
import UUID from 'vue-uuid'
import Vuex from 'vuex'
import store from './store.js'
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false
Vue.use(VueCookies)
Vue.use(UUID)
Vue.use(Vuex)

new Vue({
  render: h => h(App),
  vuetify,
  store
}).$mount('#app')
